import axios from "axios";

const httpService = axios.create({
  baseURL: "https://v1.hitokoto.cn/nm/",
  timeout: 10000
});

httpService.interceptors.request.use(
  config => {
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

httpService.interceptors.response.use(
  response => {
    return response.data;
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = "";
          break;
        case 401:
          error.message = "";
          break;
        case 403:
          error.message = "";
          break;
        case 404:
          error.message = ",";
          break;
        case 405:
          error.message = "";
          break;
        case 408:
          error.message = "";
          break;
        case 500:
          error.message = "";
          break;
        case 501:
          error.message = "";
          break;
        case 502:
          error.message = "";
          break;
        case 503:
          error.message = "";
          break;
        case 504:
          error.message = "";
          break;
        case 505:
          error.message = "http";
          break;
        default:
          error.message = ` ${error.response.status}`;
      }
    } else {
      error.message = "";
    }
    return Promise.reject(error);
  }
);

/**/

/*
 *  get
 *  url:
 *  params:
 * */
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: url,
      method: "get",
      params: params
    })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

/*
 *  post
 *  url:
 *  params:
 * */
export function post(url, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: url,
      method: "post",
      data: params
    })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

/*
 *  
 *  url:
 *  params:
 * */
export function fileUpload(url, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: url,
      method: "post",
      data: params,
      headers: {
        "Content-Type": "multipart/form-data"
      }
    })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

export default {
  get,
  post,
  fileUpload
};
