<template>
        <mu-load-more v-if="keyword != null" @refresh="reload" :refreshing="reloading" :loading="loading" @load="loadMore" loading-text=""
            :loaded-all="loadedAll">
            <mu-list textline="three-line">
                <mu-sub-header inset></mu-sub-header>
                <mu-list-item @click="addMusic(item)" v-for="item in result" :key="item.id" avatar button
                    :ripple="false">
                    <!--
                    <mu-list-item-action>
                        <mu-avatar>
                            <img :src="item.cover">
                        </mu-avatar>
                    </mu-list-item-action>
                    -->
                    <mu-list-item-content>
                        <mu-list-item-title>{{ item.name }}</mu-list-item-title>
                        <mu-list-item-sub-title>
                            <span>{{ getArtists(item.artists) }}</span>
                            <br />
                            <span>{{ item.album.name }}</span>
                        </mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>
                        <mu-button icon>
                            <mu-icon value="add"></mu-icon>
                        </mu-button>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </mu-load-more>
</template>

<script>
    import fetch from "@/utils/fetch"

    export default {
        name: "search",
        props: {
            keyword: {
                type: String,
                default: null,
                required: false
            }
        },
        mounted() {
            if (this.keyword != null) {
                this.reload()
            }
        },
        data() {
            return {
                result: [],
                page: 0,
                limit: 10,
                totalCount: -1,
                reloading: false,
                loading: false
            }
        },
        computed: {
            loadedAll() {
                if (this.totalCount == -1) {
                    return true
                }
                if (this.limit * (this.page + 1) >= this.totalCount) {
                    return true
                }
                return false
            }
        },
        methods: {
            addMusic(item) {
                this.$socket.emit("addMusic", item)
            },
            getArtists(artists) {
                let str = ""
                artists.forEach(element => {
                    str += element.name
                    str += "/"
                })
                return str.myTrim("/", "right")
            },
            reload() {
                this.reloading = true
                fetch.get("search/" + this.keyword, {
                    type: "SONG",
                    limit: this.limit + '',
                    offset: (this.page * this.limit) + ''
                }).then(result => {
                    //this.loadCover(result.result.songs)
                    this.result = result.result.songs
                    this.totalCount = result.result.songCount
                    this.reloading = false
                })
            },
            loadMore() {
                this.loading = true
                this.page += 1
                fetch.get("search/" + this.keyword, {
                    type: "SONG",
                    limit: this.limit + '',
                    offset: (this.page * this.limit) + ''
                }).then(result => {
                    //this.loadCover(result.result.songs)
                    this.result.push(...result.result.songs)
                    this.totalCount = result.result.songCount
                    this.loading = false
                })
            },
            getPosById(list, id) {
                let pos = -1;
                list.forEach((element, index) => {
                    if (element.id == id) {
                        pos = index
                        return false
                    }
                })
                return pos
            },
            loadCover(list, addition = false) {
                let ids = []
                list.forEach(item => {
                    if (item.loadedCover != true) {
                        ids.push(item.id)
                    }
                })
                fetch.get("summary/" + ids.join(","))
                    .then(result => {
                        result.ids.forEach(id => {
                            let item = result[id]
                            let pos = this.getPosById(list, id)
                            if (item != null) {
                                list[pos].cover = item.album.picture
                                list[pos].loadedCover = true
                            } else {
                                list.splice(pos, 1)
                            }
                        })
                        if (addition)
                            this.result.push(list)
                        else
                            this.result = list
                        this.reloading = false
                        this.loading = false
                    })
            }
        },
        watch: {
            keyword() {
                this.reload()
            }
        }
    }
</script>

