<template>
    <div>
        <div :class="appbarClass" class="room-appbar">
            <mu-appbar :z-depth="0" textColor="black" color="white">
                <!--
            <mu-button icon slot="left">
                <mu-icon value="arrow_back"></mu-icon>
            </mu-button>
            -->
                
                <connect-status slot="right" />
            </mu-appbar>
            <mu-container>
                <mu-flex class="search-box" justify-content="center">
                    <form class="search-form" @submit.prevent.stop="submitSearch">
                        <mu-text-field :class="inputClass" :action-icon="inputKeyword.length > 0 ? 'close' : ''"
                            :action-click="clearSearch" placeholder="" solo class="search-input" icon="search"
                            v-model="inputKeyword">
                        </mu-text-field>
                    </form>
                </mu-flex>
            </mu-container>
            <Search class="search-result" :keyword="searchKeyword" v-if="showSearch" />
        </div>
        <mu-container class="container">
            <mu-card class="card">
                <mu-card-title title="" sub-title="Play list"></mu-card-title>
                <mu-card-text>
                    <div class="playlist-empty-tip" v-if="playList.length <= 0"></div>
                    <div v-else>
                        <mu-button flat @click="clearMusic"></mu-button>
                        <mu-list textline="two-line">
                            <mu-list-item avatar v-for="item in playList" :key="item.id" :ripple="false">
                                <mu-list-item-action>
                                    <mu-avatar>
                                        <img :src="item.pic">
                                    </mu-avatar>
                                </mu-list-item-action>
                                <mu-list-item-content>
                                    <mu-list-item-title>{{ item.name/* + " id: " + item.id*/ }}</mu-list-item-title>
                                    <mu-list-item-sub-title>{{ item.artists }}</mu-list-item-sub-title>
                                </mu-list-item-content>
                                <mu-list-item-action>
                                    <mu-icon @click="removeMusic(item)" value="remove_circle"></mu-icon>
                                </mu-list-item-action>
                            </mu-list-item>
                        </mu-list>
                    </div>
                </mu-card-text>
            </mu-card>
            <mu-card class="card">
                <mu-card-title title="" sub-title="Room infomation"></mu-card-title>
                <mu-card-text>
                    <mu-list>
                        <mu-list-item button :ripple="false">
                            <mu-list-item-action>
                                <mu-icon value="account_circle"></mu-icon>
                            </mu-list-item-action>
                            <mu-list-item-title>{{ roomCreater }}</mu-list-item-title>
                        </mu-list-item>
                    </mu-list>
                    <mu-list>
                        <mu-sub-header inset></mu-sub-header>
                        <mu-list-item button :ripple="false" v-for="user in userList" :key="user">
                            <mu-list-item-action>
                                <mu-icon value="person"></mu-icon>
                            </mu-list-item-action>
                            <mu-list-item-title>{{ user }}</mu-list-item-title>
                        </mu-list-item>
                    </mu-list>
                </mu-card-text>
            </mu-card>
        </mu-container>
    </div>
</template>

<script>
    import Search from "@/components/Search"
    import ConnectStatus from "@/components/ConnectStatus"
    import {
        mapState,
        mapGetters
    } from 'vuex'

    export default {
        data() {
            return {
                inputKeyword: '',
                searchKeyword: '',
                showSearch: false
            }
        },
        name: 'room',
        components: {
            ConnectStatus,
            Search
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                if (vm.roomId == null || vm.roomId != vm.$route.params.id) {
                    vm.$socket.emit('joinRoom', vm.$route.params.id)
                }
            })
        },
        methods: {
            removeMusic(item) {
                this.$socket.emit("removeMusic", item.id)
            },
            clearMusic() {
                this.$socket.emit("clear")
            },
            clearSearch() {
                this.inputKeyword = ''
                this.searchKeyword = ''
                this.showSearch = false
            },
            submitSearch() {
                if (this.inputKeyword.length > 0) {
                    this.searchKeyword = this.inputKeyword
                    this.showSearch = true
                }
            }
        },
        computed: {
            userList() {
                let list = this.roomUserList;
                if (!list.includes(this.username)) {
                    list.push(this.username, 0)
                }
                return list
            },
            ...mapState([
                'username',
                'playList'
            ]),
            ...mapGetters([
                'roomId',
                'roomUserList',
                'roomCreater'
            ]),
            inputClass() {
                return {
                    'is-not-empty': this.inputKeyword.length > 0
                }
            },
            appbarClass() {
                return {
                    'is-searching': this.inputKeyword.length > 0
                }
            }
        }
    }
</script>

<style>
    .room-appbar {
        background-color: white;
        transition: all .15s ease-in-out;
    }

    .room-appbar.is-searching {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 100;
    }

    .search-box {
        padding: 6px 0 12px;
    }

    .search-form {
        width: 100%;
        text-align: center;
    }

    .search-input {
        margin: 0 auto;
        text-align: center;
        transition: all .15s ease-in-out;
        background-color: rgba(0, 0, 0, .1);
        border-radius: 32px;
        width: 90%;
    }

    .search-input.mu-input__focus,
    .search-input.is-not-empty {
        border-radius: 4px;
        text-align: left;
        width: 100%;
    }

    .search-result {
        height: calc(100% - 56px - 66px);
        overflow: auto;
    }

    .playlist-empty-tip {
        text-align: center;
        opacity: .54;
        font-size: 12px;
        font-weight: 400;
        letter-spacing: .08em;
    }
</style>